import "./style.css";
import * as THREE from "three";
import * as CANNON from "cannon-es";

import * as dat from "dat.gui";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";

import CannonUtils from "./utils/cannonUtils";

/**
 * Debug
 */
const gui = new dat.GUI();
const debugObject = {};
debugObject.createSphere = () => {
  createSphere(0.25 + Math.random() * 0.5, {
    x: (Math.random() - 0.5) * 3,
    y: 3,
    z: (Math.random() - 0.5) * 3,
  });
};
debugObject.createBox = () => {
  createBox(Math.random(), Math.random(), Math.random(), {
    x: (Math.random() - 0.5) * 3,
    y: 3,
    z: (Math.random() - 0.5) * 3,
  });
};
debugObject.reset = () => {
  for (const object of objectsToUpdate) {
    // Remove body
    object.body.removeEventListener("collide", playHitSound);
    world.removeBody(object.body);

    // Remove mesh
    scene.remove(object.mesh);
  }
};
debugObject.createMonkey = () => {
  createMonkey({
    x: (Math.random() - 0.5) * 5,
    y: 5,
    z: (Math.random() - 0.5) * 5,
  });
};

gui.add(debugObject, "createSphere");
gui.add(debugObject, "createBox");
gui.add(debugObject, "createMonkey");
gui.add(debugObject, "reset");

/**
 * Base
 */
// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();

// Sounds
const hitSound = new Audio("/sounds/hit.mp3");

const playHitSound = (collision) => {
  const impactStrength = collision.contact.getImpactVelocityAlongNormal();

  if (impactStrength > 1.5) {
    hitSound.volume = Math.random();
    hitSound.currentTime = 0;
    hitSound.play();
  }
};

/**
 * Textures
 */
const textureLoader = new THREE.TextureLoader();
const cubeTextureLoader = new THREE.CubeTextureLoader();

const environmentMapTexture = cubeTextureLoader.load([
  "/textures/environmentMaps/0/px.png",
  "/textures/environmentMaps/0/nx.png",
  "/textures/environmentMaps/0/py.png",
  "/textures/environmentMaps/0/ny.png",
  "/textures/environmentMaps/0/pz.png",
  "/textures/environmentMaps/0/nz.png",
]);

/**
 * Physics
 */
// World
const world = new CANNON.World();
world.broadphase = new CANNON.SAPBroadphase(world);
world.allowSleep = true;
world.gravity.set(0, -9.82, 0);

// Materials

// Simplified version
const defaultMaterial = new CANNON.Material("default");

const defaultContectMaterial = new CANNON.ContactMaterial(
  defaultMaterial,
  defaultMaterial,
  {
    friction: 0.1,
    restitution: 0.7,
  }
);
world.addContactMaterial(defaultContectMaterial);
world.defaultContactMaterial = defaultContectMaterial;

// Floor
const floorShape = new CANNON.Plane();
const floorBody = new CANNON.Body();
floorBody.mass = 0;
floorBody.addShape(floorShape);
floorBody.quaternion.setFromAxisAngle(new CANNON.Vec3(-1, 0, 0), Math.PI * 0.5);
world.addBody(floorBody);

/**
 * Floor
 */
const floor = new THREE.Mesh(
  new THREE.PlaneGeometry(10, 10),
  new THREE.MeshStandardMaterial({
    color: "#777777",
    metalness: 0.3,
    roughness: 0.4,
    envMap: environmentMapTexture,
  })
);
floor.receiveShadow = true;
floor.rotation.x = -Math.PI * 0.5;
floor.position.set(0, 0, 0);
floorBody.position.copy(floor.position);
scene.add(floor);

// Utils
const objectsToUpdate = [];

/**
 * Models
 */
const gltfLoader = new GLTFLoader();
const shapes = {
  TRIMESH: "trimesh",
  CONVEXPOLYHEDRON: "convexpolyhedron",
};

let monkeyBundle = null;
const monkeyMaterial = new THREE.MeshNormalMaterial();
const monkeyHullMaterial = new THREE.MeshBasicMaterial({
  color: "cyan",
  wireframe: true,
});

const parameters = {
  shapeType: shapes.CONVEXPOLYHEDRON,
  colliderVisibility: true,
};

const colliderFolder = gui.addFolder("Colliders");
colliderFolder.open();
colliderFolder
  .add(parameters, "shapeType", {
    Trimesh: shapes.TRIMESH,
    Convexpolyhedron: shapes.CONVEXPOLYHEDRON,
  })
  .onChange(() => {
    debugObject.reset();
    createMonkey({ x: 0, y: 2, z: 0 });
  });
colliderFolder.add(parameters, "colliderVisibility").onChange(() => {
  debugObject.reset();
  createMonkey({ x: 0, y: 2, z: 0 });
});

const utils = new CannonUtils();

gltfLoader.load("/models/monkeyPhysics.glb", (gltf) => {
  // Scene with Monkey Mesh and Convex Monkey Meshes (to use as colliders)
  monkeyBundle = gltf.scene;

  monkeyBundle.children[0].material = monkeyMaterial;
  monkeyBundle.children[0].visible = true;

  createMonkey({ x: 0, y: 2, z: 0 });
});

const createMonkey = (spawnPosition) => {
  if (monkeyBundle) {
    const monkey = monkeyBundle.clone();
    const monkeyBody = new CANNON.Body({ mass: 1 });

    monkey.children.forEach((child) => {
      // Convex Meshes Generated with https://github.com/andyp123/blender_vhacd
      if (child.name.includes("hull")) {
        child.material = monkeyHullMaterial;

        if (parameters.shapeType === shapes.TRIMESH) {
          const monkeyShape = utils.createTrimesh(child.geometry);
          monkeyBody.addShape(monkeyShape);

          // Debug Line Trimesh
          let geometry = new THREE.BufferGeometry();
          let points = [];
          for (let i = 0; i < monkeyShape.vertices.length; i += 3) {
            points.push(
              new THREE.Vector3(
                monkeyShape.vertices[i],
                monkeyShape.vertices[i + 1],
                monkeyShape.vertices[i + 2]
              )
            );
          }
          geometry.setFromPoints(points);
          const debugTrimesh = new THREE.Mesh(geometry, monkeyHullMaterial);
          if (!parameters.colliderVisibility) {
            debugTrimesh.visible = false;
          }
          monkeyShape.geometryId = geometry.id;
          monkey.add(debugTrimesh);
        } else if (parameters.shapeType === shapes.CONVEXPOLYHEDRON) {
          const monkeyShape = utils.createConvexPolyhedron(child.geometry);
          monkeyBody.addShape(monkeyShape);

          // Debug Line Convex
          let geometry = new THREE.BufferGeometry();
          let points = [];
          for (let i = 0; i < monkeyShape.vertices.length; i += 1) {
            const v = monkeyShape.vertices[i];
            points.push(new THREE.Vector3(v.x, v.y, v.z));
          }
          geometry.setFromPoints(points);
          const debugConvex = new THREE.Mesh(geometry, monkeyHullMaterial);
          if (!parameters.colliderVisibility) {
            debugConvex.visible = false;
          }
          monkeyShape.geometryId = geometry.id;
          monkey.add(debugConvex);
        }

        child.visible = false;
      }
    });

    monkey.position.set(spawnPosition.x, spawnPosition.y, spawnPosition.z);
    monkeyBody.position.copy(monkey.position);

    world.addBody(monkeyBody);
    scene.add(monkey);

    monkeyBody.addEventListener("collide", playHitSound);

    objectsToUpdate.push({
      body: monkeyBody,
      mesh: monkey,
    });
  }
};

/**
 * Lights
 */
const ambientLight = new THREE.AmbientLight(0xffffff, 0.7);
scene.add(ambientLight);

const directionalLight = new THREE.DirectionalLight(0xffffff, 0.2);
directionalLight.castShadow = true;
directionalLight.shadow.mapSize.set(1024, 1024);
directionalLight.shadow.camera.far = 15;
directionalLight.shadow.camera.left = -7;
directionalLight.shadow.camera.top = 7;
directionalLight.shadow.camera.right = 7;
directionalLight.shadow.camera.bottom = -7;
directionalLight.position.set(5, 5, 5);
scene.add(directionalLight);

/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

window.addEventListener("resize", () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(
  75,
  sizes.width / sizes.height,
  0.1,
  100
);
camera.position.set(-3, 3, 3);
scene.add(camera);

// Controls
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
});
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = THREE.PCFSoftShadowMap;
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

// Sphere
const sphereGeometry = new THREE.SphereBufferGeometry(1, 20, 20);
const sphereMaterial = new THREE.MeshStandardMaterial({
  metalness: 0.3,
  roughness: 0.4,
  envMap: environmentMapTexture,
});
const createSphere = (radius, position) => {
  // Three.js mesh
  const mesh = new THREE.Mesh(sphereGeometry, sphereMaterial);
  mesh.scale.set(radius, radius, radius);
  mesh.castShadow = true;
  mesh.position.copy(position);
  scene.add(mesh);

  // Cannon.js body
  const shape = new CANNON.Sphere(radius);
  const body = new CANNON.Body({
    mass: 1,
    position: new CANNON.Vec3(0, 3, 0),
    shape,
    material: defaultMaterial,
  });
  body.position.copy(position);
  body.addEventListener("collide", playHitSound);
  world.addBody(body);

  // Save in objects to update
  objectsToUpdate.push({
    mesh,
    body,
  });
};

// Box
const boxGeometry = new THREE.BoxBufferGeometry(1, 1, 1);
const boxMaterial = new THREE.MeshStandardMaterial({
  metalness: 0.3,
  roughness: 0.4,
  envMap: environmentMapTexture,
});
const createBox = (width, height, depth, position) => {
  // Three.js mesh
  const mesh = new THREE.Mesh(boxGeometry, boxMaterial);
  mesh.scale.set(width, height, depth);
  mesh.castShadow = true;
  mesh.position.copy(position);
  scene.add(mesh);

  // Cannon.js body
  const shape = new CANNON.Box(
    new CANNON.Vec3(width * 0.5, height * 0.5, depth * 0.5)
  );
  const body = new CANNON.Body({
    mass: 1,
    position: new CANNON.Vec3(0, 3, 0),
    shape,
    material: defaultMaterial,
  });
  body.position.copy(position);
  body.addEventListener("collide", playHitSound);
  world.addBody(body);

  // Save in objects to update
  objectsToUpdate.push({
    mesh,
    body,
  });
};

/**
 * Animate
 */
const clock = new THREE.Clock();
let oldElapsedTime = 0;

const tick = () => {
  const elapsedTime = clock.getElapsedTime();
  const deltaTime = elapsedTime - oldElapsedTime;
  oldElapsedTime = elapsedTime;

  // Update Physics World
  world.step(1 / 60, deltaTime, 3);

  for (const object of objectsToUpdate) {
    object.mesh.position.copy(object.body.position);
    object.mesh.quaternion.copy(object.body.quaternion);
  }

  // Update controls
  controls.update();

  // Render
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();
